import React from 'react';
import car from './car.svg';
import coins from './coins.svg';
import photo from './photo.svg';
import timer from './timer.svg';

const InfoBlock = () => {
	let profit = [
		{ icon: car, text: '   3 .  .' },
		{ icon: photo, text: ' .    .' },
		{ icon: coins, text: '       .' },
		{ icon: timer, text: '  .   ' }
	];
	return (
		<>
			{' '}
			<div className='profits'>
				{profit.map(el => {
					return (
						<div
							className='profit'
							key={Math.random()
								.toString(36)
								.substring(2, 9)}
						>
							<div className='profit-icon'>
								<img src={el.icon} alt='' />
							</div>
							<div className='profit-text'>{el.text}</div>
						</div>
					);
				})}
			</div>
			<div className='transfer-about'>
				<h1 className='transfer-heading'></h1>
				<div className='transfer-text'>
					<p>
						        2019 ?   
						   :  , ,
						,   ...  ,   .
						       , 
						.        .
					</p>
					<br />
					<p>
						         . 
						      .   
						 .     ,  
						  ,      .
					</p>
				</div>
				<div className='button-group page'>
					<div className='button-orange'> </div>
					<div className='button-orange button-orange-o'> </div>
				</div>
			</div>
		</>
	);
};

export default InfoBlock;
