import React, { useState } from 'react';
import Input from './../input/Input';

const ContactForm = () => {
	const [name, changeName] = useState('');
	const [mail, changeMail] = useState('');

	const pushNotice = () => {
		if (name.length > 5 && mail.length > 5)
			alert('      ');
		else alert('     ');
	};

	return (
		<form className='contact_form' action=''>
			<h3 className='contact_form_title'>   </h3>
			<Input
				value={name}
				changeValue={changeName}
				label='Email/'
				type='text'
				className='contacts-email'
			/>
			<Input
				value={mail}
				changeValue={changeMail}
				label=''
				type='text'
				className='contacts-name'
			/>
			<button
				className='button-orange button-orange-o'
				value=' '
				onClick={e => {
					pushNotice();
					e.preventDefault();
				}}
			>
				 
			</button>
		</form>
	);
};

export default ContactForm;
