import React, { useState, useEffect, useContext } from 'react';
import SliderSection from '../../components/sliderSection/sliderSection';
import { Ctx } from '../../store';
import road from './road.jpg';
import mount_river from './mount_river.jpg';
import mount_lake from './mount_lake.jpg';

const AboutPage = () => {
	const { store } = useContext(Ctx);
	const [loaded, changeLoaded] = useState(false);
	const [DBPageContent, changeDBPageContent] = useState();

	const [pageContent] = useState({
		about_family: { id: '', name: '', text: '', heading: '' },
		about_onaltai: { id: '', name: '', text: '', heading: '' },
		about_rest: { id: '', name: '', text: '', heading: '' }
	});

	const textLoad = '...';

	const created = async () => {
		await getData();
	};

	const getData = async () => {
		fetch(store.apiUrl + '/api/settings/all')
			.then(res => res.json())
			.then(res => {
				changeDBPageContent(res.data);
			})
			.catch(err => console.warn(err));
	};

	const successLoaded = async () => {
		changeLoaded(true);
	};

	const getContentObject = async () => {
		for (let key in pageContent) {
			DBPageContent.map(x => {
				if (x.name === key) {
					pageContent[key].id = x.id;
					pageContent[key].name = x.name;
					pageContent[key].text = x.text;
					pageContent[key].heading = x.heading;
				}
			});
		}
		console.log(pageContent.about_family.text);
		await successLoaded();
	};

	useEffect(() => {
		created();
	}, []);

	useEffect(() => {
		if (DBPageContent && DBPageContent.length !== 0) getContentObject();
	}, [DBPageContent]);

	if (!DBPageContent || DBPageContent.length === 0) return '...';

	console.log(pageContent);

	return (
		<div>
			<section className='banner'>
				<div className='banner_description'>
					<h2 className='banner_description_title'> </h2>
					<div className='banner_description_subtitle'>
						    
					</div>
					<div className='banner_description_text'>
						        ,
						  .
						<br />   , a    .
					</div>
					<div
						className='about-link'
						onClick={e => {
							e.preventDefault();
							window.scrollTo({
								behavior: 'smooth',
								top: document.getElementById('second_screen').offsetTop
							});
						}}
					>
						<svg
							className='banner_scroll'
							width='27'
							height='30'
							viewBox='0 0 27 30'
							fill='white'
							xmlns='http://www.w3.org/2000/svg'
						>
							<g clipPath='url(#clip0)'>
								<path d='M25.9863 5.77148L13.623 18.1406C13.3477 18.416 12.9023 18.416 12.627 18.1406L0.263672 5.77148C-0.0117188 5.49609 -0.0117188 5.05078 0.263672 4.77539L0.679688 4.35937C0.955078 4.08398 1.40039 4.08398 1.67578 4.35937L13.125 15.8145L24.5801 4.36523C24.8555 4.08984 25.3008 4.08984 25.5762 4.36523L25.9922 4.78125C26.2617 5.05078 26.2617 5.49609 25.9863 5.77148ZM25.9863 12.2754L25.5703 11.8594C25.2949 11.584 24.8496 11.584 24.5742 11.8594L13.125 23.3145L1.66992 11.8652C1.39453 11.5898 0.949219 11.5898 0.673828 11.8652L0.257812 12.2812C-0.0175781 12.5566 -0.0175781 13.002 0.257812 13.2773L12.6211 25.6465C12.8965 25.9219 13.3418 25.9219 13.6172 25.6465L25.9805 13.2773C26.2617 12.9961 26.2617 12.5508 25.9863 12.2754Z' />
							</g>
							<defs>
								<clipPath id='clip0'>
									<rect width='26.25' height='30' />
								</clipPath>
							</defs>
						</svg>
					</div>
				</div>
			</section>
			<div className='wrapper' id='second_screen'>
				<div>
					<SliderSection
						images={[mount_lake, road, mount_river]}
						title={!loaded ? textLoad : pageContent.about_rest.heading}
						button=' '
					>
						{!loaded ? textLoad : pageContent.about_rest.text}
					</SliderSection>

					<SliderSection
						images={[mount_lake, road, mount_river]}
						title={!loaded ? textLoad : pageContent.about_onaltai.heading}
						position='1'
					>
						{!loaded ? textLoad : pageContent.about_onaltai.text}
					</SliderSection>

					<SliderSection
						images={[mount_lake, road, mount_river]}
						title={!loaded ? textLoad : pageContent.about_family.heading}
					>
						{!loaded ? textLoad : pageContent.about_family.text}
					</SliderSection>
				</div>
			</div>
		</div>
	);
};

export default AboutPage;
